body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  height: 100vh;
  background: white; }

* {
  box-sizing: border-box; }

.app {
  height: 100vh; }

.hp__page {
  margin: 0, auto;
  height: 100%;
  display: grid;
  grid-template-rows: 100px, 1fr;
  grid-template-areas: "header", "main"; }

.hp__header {
  grid-area: "header";
  display: flex;
  justify-content: center;
  align-items: center; }

.hp__header-title {
  margin: 0;
  font-size: 60px; }

.hp__main {
  grid-area: "main"; }

.hp__filter {
  display: flex;
  justify-content: center;
  align-items: center; }

.hp__filter-input {
  width: 400px;
  height: 28px;
  text-align: center; }

.hp__detail {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: lightcyan; }

.hp__detail-card {
  background-color: #f88553;
  border: 1px solid black;
  color: white;
  width: 600px;
  height: 300px;
  display: flex; }

.detail__card-databox {
  padding: 10px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between; }

.detail__card-imagebox {
  height: 100%;
  overflow: hidden; }

.detail__card-image {
  height: 100%; }

.detail__card-name {
  margin: 0; }

.detail__card-house {
  margin: 0; }

.detail__card-born {
  margin: 0; }

.detail__card-patronus {
  margin: 0; }

.detail__card-status {
  margin: 0; }

.hp-detail-link {
  text-align: center;
  padding: 8px;
  height: 40px;
  border: 2px solid black;
  border-radius: 10px; }

.hp__container {
  display: flex;
  justify-content: center;
  align-items: center; }

.hp__list {
  display: grid;
  grid-template-columns: 180px 180px 180px 180px;
  grid-auto-rows: 400px;
  grid-gap: 20px;
  padding: 0; }

.hp__list-item {
  background-color: grey;
  border: 1px solid black;
  list-style-type: none;
  color: white;
  display: grid;
  grid-template-rows: 240px 1fr; }

.hp-list-link {
  text-align: center;
  padding: 8px;
  height: 40px;
  border: 2px solid black;
  border-radius: 10px; }

.list__card-databox {
  height: 160px;
  padding: 10px;
  background-color: #f88553;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between; }

.list-card-imagebox {
  height: 240px;
  overflow: hidden; }

.list__card-image {
  min-height: 100%;
  width: 100%; }

.list__card-name {
  margin: 0; }

.list__card-house {
  margin: 0; }

